html {
  font-size: 62.5%;
  // 1 rem = 16px * 62.5% / 100 = 10px 
}

body {
  margin: 0;
  padding: 0;

  font-family: @circe;
  font-weight: 400;
  // font-size: 16px;
  // font-size: 62.5%;
  font-size: 1.6rem;
  background-color: @bgc;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  display: block;
  text-decoration: none;
  color: #000;
}

ul {
  .reset-list();
}

h1,
h2,
h3,
h4,
p {
  .mp-reset(0,0);
  color: #000;
}

input,
button {
  box-sizing: border-box;
  padding: 0;
  border: none;
  background-color: transparent;
}

button,
input,
optgroup,
select,
textarea {
  font-family: @circe;
}

.noscroll {
  overflow: hidden !important;
  height: 100% !important;
}

.overlay {
  display: none;

  z-index: 99;
  position: fixed;
  top: 0;
  left: 0;

  opacity: 1;
  visibility: visible;

  width: 100%;
  height: 100%;

  background-color: rgba(0,0,0,.5);

  &.active {
    display: block;
  }
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  // max-width: 1410px;
  width: 141rem;
  .margin-inline(@a);
  .padding-inline(2rem);
}

.container-outside {
  // max-width: 1510px;
  width: 151rem;
  .margin-inline(@a);
  .padding-inline(2rem);
}

.mobile-block {
  display: none;
}

.desktop-block {
  display: block;
}

// .phone-mask:-moz-submit-invalid,
.phone-mask:-moz-ui-invalid {
  box-shadow: none;
}

.subtitle {
  position: relative;
  display: inline-block;
  font-weight: 700;
  font-size: 1.8rem;
  &::after {
    content: '';
    position: absolute;
    bottom: 0.6rem;
    height: 0.2rem;
    background-color: #000;
  }
}

.link {
  position: relative;
  display: inline-flex;
  align-items: center;

  font-weight: bold;
  font-size: 1.8rem;
  cursor: pointer;
  &--circle {
    &::before {
      content: '';
      position: absolute;
      z-index: -1;
      top: -1.7rem;
      left: -3rem;
      
      width: 7rem;
      height: 7rem;
      border: 0.3rem solid #fff;
      border-radius: 50%;
    }
  }
}

.stroke {
  color: transparent;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #353535;

  &--white {
    -webkit-text-stroke-color: #fff;
  }
}

.icon-arrow {
  width: 5rem;
  height: 5rem;
  &--right {
    padding-left: 1.1rem;
  }
  &--left {
    padding-right: 1.1rem;
  }
  &--top {
    transform: rotate(-90deg);
  }
  &--bottom {
    transform: rotate(90deg);
  }
}


@media (max-width: @tablet-width) {
  .container,
  .container-outside {
    width: @a;
    .padding-inline(2.5rem);
  }

  .desktop-block {
    display: none;
  }

  
  .mobile-block {
    display: block;
  }

  .subtitle {
    font-size: 1.8rem;
    &::after {
      bottom: 0.6rem;
      height: 0.2rem;
      border-radius: 0.2rem;
    }
  }

  .link {
    &--circle {
      &::before {
        top: -1.7rem;
        left: -3rem;
        
        width: 7rem;
        height: 7rem;
        border: 0.3rem solid #fff;
        border-radius: 50%;
      }
    }
  }
}

