.two-columns {
  margin-bottom: 8.8rem;

  &--mb-bigger {
    margin-bottom: 12.5rem;
  }
  &--mt {
    margin-top: 21.5rem;
  }
  &--btn-back {
    margin-top: 9.5rem;
  }
}

.two-columns__main-title {
  margin-bottom: 6.6rem;

  font-weight: 800;
  font-size: 8rem;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.two-columns__column-wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.two-columns__column-center {
  width: 107rem;
}

.two-columns__link-back {
  margin-bottom: 7rem;
  font-size: 2.2rem;
}

.two-columns__icon-arrow {
}

.two-columns__icon-arrow--left {
}

.two-columns__column-left {
  width: 56.9rem;
}

.two-columns__title {
  max-width: 460px;
  margin-bottom: 3rem;

  font-weight: 700;
  font-size: 6.5rem;
  line-height: 7rem;

  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #000;
}

.two-columns__subtitle {
  margin-bottom: 3.9rem;

  font-weight: 400;
  font-size: 3rem;
  line-height: 4.4rem;
}

.two-columns__link-blank {
  margin-bottom: 4.1rem;
  font-size: 2.4rem;
  line-height: 3.5rem;
}

.two-columns__signature {
  font-weight: 400;
  font-size: 2rem;
  line-height: 2.9rem;
}

.two-columns__column-right {
  width: 76.6rem;
}

.two-columns__desc {
  margin-bottom: 2.7rem;

  font-size: 1.8rem;
  line-height: 2.7rem;
}

.two-columns__list-title {
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 2.7rem;
}

.two-columns__list {
  counter-reset: articleItem;
  margin-bottom: 2.7rem;
}

.two-columns__item {
  position: relative;

  font-size: 1.8rem;
  line-height: 2.7rem;

  &:first-letter {
    padding-left: 20px;
  }

  &::after {
    counter-increment: articleItem;
    content: counter(articleItem, decimal-leading-zero);
    
    position: absolute;
    top: -0.4rem;
    left: 2px;

    font-weight: 800;
    font-size: 10px;

    -webkit-text-stroke-width: 0.1px;
    -webkit-text-stroke-color: #000;
  }
}

.two-columns__img-wrap {
  margin-top: 9rem;
  width: 100%;
  height: 45.5rem;

  &--img-top {
    height: 33.1rem;
    margin-top: 0;
    margin-bottom: 9.6rem;
  }
}

.two-columns__img {
  .img-cover;
}

.two-columns__article-title {
  margin-bottom: 2.2rem;

  font-weight: 700;
  font-size: 3rem;
  line-height: 4.4rem;
}

@media (max-width: @tablet-width) {
  .two-columns {
    margin-bottom: 3.2rem;
    &--mb-bigger {
      margin-bottom: 11.2rem;
    }
    &--mt,
    &--btn-back {
      margin-top: 6rem;
    }
  }

  .two-columns__main-title {
    display: flex;
    // flex-direction: column-reverse;
    flex-direction: column;
    margin-bottom: 0.6rem;
    
    font-size: 4rem;
    line-height: 5.9rem;
    letter-spacing: normal;

    &--stroke {
      letter-spacing: 0.05em;
    }
  }

  .two-columns__column-left,
  .two-columns__column-right,
  .two-columns__column-center {
    width: @a;
  }

  .two-columns__link-back {
    margin-bottom: 2rem;
  }

  .two-columns__column-left {
    margin-bottom: 3.8rem;
  }

  .two-columns__feedback {
    order: 2;
    margin-top: 15.9rem;
  }

  .two-columns__title {
    max-width: none;
    margin-bottom: 3.7rem;
    font-size: 5rem;
    line-height: 6rem;
  }

  .two-columns__subtitle {
    font-size: 2.4rem;
    line-height: 3.5rem;
    margin-bottom: 4.2rem;
  }

  .two-columns__signature {
    margin-bottom: 0;
  }

  .two-columns__img-wrap {
    margin-top: 1.7rem;
    height: 14.5rem;
    &--img-top {
      height: 26.5rem;
      margin-bottom: 7.4rem;
    }
  }
}

